import * as React from 'react';
import Form from 'react-bootstrap/Form';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';

import { sortSubjects } from '../util/util.js';

export default function SubjectTable({ subjects, courses, currentCourse, dispatch}) {
  let course = courses[currentCourse];
  let allSubjects = sortSubjects(course.coreSubjects.concat(course.majorSubjects).concat(course.electiveSubjects));
  return (
    <Form>
      <table className='table table-striped table-sm'>
        <thead>
            <tr>
              <th scope='col'>Code</th>
              <th scope='col'>Name</th>
              <th scope='col'>Timing</th>
            </tr>
        </thead>
        <tbody>
          { 
            allSubjects.map(code => (
              <tr key={code}>
                <td>{code}</td>
                <td>{subjects[code].name}</td>
                <td>
                  <ToggleButtonGroup type='radio' name={`${code}-tp-selector`} defaultValue={course.schedule[code]} onChange={tp => dispatch({ type: 'setSchedule', code, tp })}>
                    <ToggleButton id={`radio-${code}-none`} variant='outline-primary' size='sm' value=''>None</ToggleButton>
                    {course.teachingPeriods.map(tp => (
                      <ToggleButton key={tp} id={`radio-${code}-${tp}`} variant='outline-primary' size="sm" value={tp}>
                        {tp.replace('Year ', 'Y')} 
                      </ToggleButton>
                    ))}
                  </ToggleButtonGroup>
                </td>
              </tr>
            ))
          }
        </tbody>
      </table>
    </Form>
)
}
