import * as React from 'react';
import { useState, useReducer } from 'react';
import './App.css';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Form from 'react-bootstrap/Form';

import SubjectTable from './components/SubjectTable';
import SubjectOfferings from './components/SubjectOfferings';
import SubjectGraph from './components/SubjectGraph';
import JsonEditor from './components/JsonEditor';
import { initialState } from './state/default';
import reducer from './state/reducer';

function App() {
  const [editTarget, setEditTarget] = useState(null);
  const [state, dispatch] = useReducer(reducer, initialState);
  
  if (editTarget === null) {
    return (<>
      <div className='container'>
        <div className='row'>
          <div className='col'>
            <h1>Study planner</h1>
            Choose a course:
            <Form.Select onChange={e => dispatch({type: 'setCurrentCourse', payload: e.target.value})} value={state.currentCourse}>
              {
                Object.entries(state.courses).map(([code, course]) => (
                  <option key={code} value={code}>{course.name}</option>
                ))
              }
            </Form.Select>
            <p></p>
            <p></p>
            
          </div>
        </div>
      </div>
      <div className='container-fluid'>
        <div className='row'>
          <div className='col col-xl-6'>
            <h1>Subject timing</h1>
            <SubjectTable subjects={state.subjects} courses={state.courses} currentCourse={state.currentCourse} dispatch={dispatch} />
          </div>
          <div className='col col-xl-6'>
            <h1>Flowchart</h1>
            <SubjectGraph subjects={state.subjects} courses={state.courses} currentCourse={state.currentCourse} />
          </div>
        </div>
        <div className='row'>
          <h1>Extras</h1>
          <Accordion>
            <Accordion.Item eventKey="0">
              <Accordion.Header>Summary of subject offerings</Accordion.Header>
              <Accordion.Body>
                <SubjectOfferings subjects={state.subjects} dispatch={dispatch} courses={state.courses} />
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>

          <Accordion>
            <Accordion.Item eventKey="0">
              <Accordion.Header>Edit configuration</Accordion.Header>
              <Accordion.Body>
                <div><Button variant="secondary" onClick={() => setEditTarget("Subjects")}>Edit subject database</Button></div>
                <div><Button variant="secondary" onClick={() => setEditTarget("Courses")}>Edit course database</Button></div>
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </div>
      </div>
    </>)
  } else {
    return (
        <JsonEditor value={state[editTarget.toLowerCase()]} onClose={(newValue) => {
            dispatch({ type: `set${editTarget}`, payload: newValue });
            setEditTarget(null);
        }} />
    )
  }
}

export default App;
