export default function reducer(state, action) {
    console.log("Received action: ", action);

    switch (action.type) {
        case "setSubjects":
            return { ...state, subjects: action.payload };
        case "setCourses":
            return { ...state, courses: action.payload };
        case "setCurrentCourse":
            return { ...state, currentCourse: action.payload };
        // case "addOffering":
        //     return {
        //         ...state,
        //         subjects: {
        //             ...state.subjects,
        //             [action.code]: {
        //                 ...state.subjects[action.code],
        //                 offerings: [...state.subjects[action.code].offerings || [], action.tp]
        //             }
        //         }
        //     };
        // case "removeOffering":
        //     return {
        //         ...state,
        //         subjects: {
        //             ...state.subjects,
        //             [action.code]: {
        //                 ...state.subjects[action.code],
        //                 offerings: state.subjects[action.code].offerings.filter(tp => tp !== action.tp)
        //             }
        //         }
        //     };
        case "setSchedule":
            if (action.tp === "") {
                // remove the subject from the schedule 
                let newSchedule = { ...state.courses[state.currentCourse].schedule };
                delete newSchedule[action.code];
                return {
                    ...state,
                    courses: {
                        ...state.courses,
                        [state.currentCourse]: {
                            ...state.courses[state.currentCourse],
                            schedule: newSchedule
                        }
                    }
                };
            } 
            return {
                ...state,
                courses: {
                    ...state.courses,
                    [state.currentCourse]: {
                        ...state.courses[state.currentCourse],
                        schedule: {
                            ...state.courses[state.currentCourse].schedule,
                            [action.code]: action.tp
                        }
                    }
                }
            };
        default: {
            throw Error('Unknown action: ' + action.type);
        }
    }
}
