import * as React from 'react';
import { useState } from 'react';
import Button from 'react-bootstrap/Button';

export default function JsonEditor({ value, onClose }) {
  const [text, setText] = useState(JSON.stringify(value, null, 2));
  const [error, setError] = useState(null);

  const handleChange = (event) => {
    const newText = event.target.value;
    setText(newText);
    try {
      const newValue = JSON.parse(newText);
      // parse succeeded
      setError(null);
    } catch (error) {
      setError(error.toString());
    }
  };

  return (
    <>
      <h1>Editing configuration</h1>
      <textarea rows={30} value={text} onChange={handleChange} style={{ width: '100%' }} />
      {error && <div style={{ color: "red" }}>{error}</div>}
      <Button variant='primary' disabled={error !== null} onClick={() => onClose(JSON.parse(text))}>Save changes</Button>
    </>
  );
}
