import Form from 'react-bootstrap/Form';
import { sortSubjects } from '../util/util';

export default function SubjectOfferings({subjects, dispatch, courses}) {

    function isSubjectOfferedInStudyPeriod(subject, studyPeriod) {
        return Object.entries(courses).some(([code, course]) => course.schedule[subject]?.includes(studyPeriod));
    }
    // function coursesUsingSubjectInStudyPeriod(subject, studyPeriod) {
    //     return Object.entries(courses).filter(([code, course]) => course.schedule[subject]?.includes(studyPeriod)).map(([code, course]) => code);
    // }

    let codes = sortSubjects(Object.keys(subjects));

    return (
        <table className='table table-striped'>
            <thead>
                <tr>
                    <th scope='col'>Code</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>TR1</th>
                    <th scope='col'>TR2</th>
                    <th scope='col'>TR3</th>
                </tr>
            </thead>
            <tbody>
                { 
                    codes.map(code => (
                        <tr key={code}>
                            <td>{code}</td>
                            <td>{subjects[code].name}</td>
                            {["TR1", "TR2", "TR3"].map(tp => (
                                <td key={tp}>
                                    {isSubjectOfferedInStudyPeriod(code, tp) ? "Yes" : ""}
                                </td>
                            ))}
                        </tr>
                    ))
                }
            </tbody>
        </table>
    )
}
